<template>
  <div class="page-container">
    <van-nav-bar
      title=""
      class="nav-bar"
      left-arrow
      @click-left="$router.go(-1)"
      border="false"
    >
      <template #title>
        <span style="color: white; font-weight: 600"></span>
      </template>
    </van-nav-bar>

    <!--  -->
    <div class="card" style="margin: 20px 15px">
      <div class="card-body" style="padding: 30px 20px">
        <div style="text-align: center; margin-bottom: 25px">
          <h2
            style="margin-top: 15px; font-weight: 600; color: var(--text-color)"
          >
            
          </h2>
          <p style="color: var(--text-light); margin-top: 5px">
            
          </p>
        </div>

        <van-cell-group border="false">
          <van-field
            v-model="username"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
            style="margin-bottom: 12px"
          />
          <van-field
            v-model="password"
            type="password"
            label=""
            placeholder="6"
            :rules="[
              { required: true, message: '' },
              { min: 6, message: '6' },
            ]"
            style="margin-bottom: 12px"
          />
          <van-field
            v-model="confirmPassword"
            type="password"
            label=""
            placeholder=""
            :rules="[
              { required: true, message: '' },
              { validator: validatePassword, message: '' },
            ]"
          />
        </van-cell-group>

        <van-button
          type="primary"
          class="btn-primary"
          style="width: 100%; margin-top: 25px; height: 48px; font-size: 16px"
          @click="handleRegister"
          :loading="loading"
        >
          
        </van-button>
      </div>
    </div>

    <div style="text-align: center; margin-top: 15px">
      <span style="color: var(--text-gray)"></span>
      <a
        @click="$router.push('/login')"
        style="color: var(--primary-color); font-weight: 500; margin-left: 5px"
        ></a
      >
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import AV from "@/utils/leancloud";

export default {
  name: "RegisterPage",
  data() {
    return {
      username: "",
      password: "",
      confirmPassword: "",
      loading: false,
    };
  },
  methods: {
    validatePassword() {
      return this.password === this.confirmPassword;
    },
    async handleRegister() {
      try {
        if (!this.validatePassword()) {
          return Toast.fail("");
        }

        this.loading = true;
        const user = new AV.User();
        user.setUsername(this.username);
        user.setPassword(this.password);
        await user.signUp();

        Toast.success("");
        setTimeout(() => {
          this.$router.push("/login");
        }, 1500);
      } catch (error) {
        Toast.fail("" + error.message);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>
