<script>
import moment from "moment";
import AV from "@/utils/leancloud";

export default {
  name: "StatisticsPage",
  data() {
    return {
      // 
      showStartDatePicker: false,
      showEndDatePicker: false,
      startDate: moment().startOf("month").format("YYYY-MM-DD"), // 
      endDate: moment().format("YYYY-MM-DD"), // 
      startDateValue: new Date(moment().startOf("month").format("YYYY-MM-DD")),
      endDateValue: new Date(),
      minDate: new Date("2020-01-01"),
      maxDate: new Date(),

      // 
      loading: false,
      finished: false,
      statisticsData: {}, // { : [] }
      currentDetail: null, // 
      showDetailPopup: false,

      // 
      imageStates: {
        morningStartImg: null,
        morningEndImg: null,
        afternoonStartImg: null,
        afternoonEndImg: null,
      },
      imageTimestamp: {}, // 
    };
  },
  created() {
    // 
    this.fetchStatistics();
  },
  methods: {
    // 
    handleDateConfirm(date, type) {
      const dateStr = moment(date).format("YYYY-MM-DD");
      if (type === "start") {
        this.startDate = dateStr;
        this.startDateValue = date;
      } else {
        this.endDate = dateStr;
        this.endDateValue = date;
      }
      this[
        type === "start" ? "showStartDatePicker" : "showEndDatePicker"
      ] = false;
    },

    //  - 
    async fetchStatistics() {
      try {
        this.loading = true;
        this.statisticsData = {};

        // ID + 
        const query = new AV.Query("WorkRecord");
        query.equalTo("userId", AV.User.current().id);
        query.greaterThanOrEqualTo("date", this.startDate);
        query.lessThanOrEqualTo("date", this.endDate);
        query.descending("date"); // 

        const results = await query.find();
        if (results.length === 0) {
          this.loading = false;
          return;
        }

        //  - 
        results.forEach((item) => {
          const personnelName = item.get("personnelName") || "";
          const record = {
            id: item.id,
            personnelName,
            date: item.get("date"),
            morningStart: item.get("morningStart"),
            morningEnd: item.get("morningEnd"),
            afternoonStart: item.get("afternoonStart"),
            afternoonEnd: item.get("afternoonEnd"),
            morningHours: item.get("morningHours") || 0,
            afternoonHours: item.get("afternoonHours") || 0,
            totalHours: item.get("totalHours") || 0,
            // URLLeanCloud
            morningStartImg: this.getImageUrlFromRecord(
              item,
              "morningStartImg"
            ),
            morningEndImg: this.getImageUrlFromRecord(item, "morningEndImg"),
            afternoonStartImg: this.getImageUrlFromRecord(
              item,
              "afternoonStartImg"
            ),
            afternoonEndImg: this.getImageUrlFromRecord(
              item,
              "afternoonEndImg"
            ),
          };

          // 
          if (!this.statisticsData[personnelName]) {
            this.statisticsData[personnelName] = [];
          }
          this.statisticsData[personnelName].push(record);
        });
      } catch (error) {
        console.error("", error);
        this.$toast.fail("");
      } finally {
        this.loading = false;
        this.finished = true;
      }
    },

    // LeanCloudURL
    getImageUrlFromRecord(record, fieldName) {
      const value = record.get(fieldName);
      if (!value) return "";

      // LeanCloudURL
      if (value && typeof value.getUrl === "function") {
        return value.getUrl();
      }

      // URL
      if (typeof value === "string" && value.startsWith("http")) {
        return value;
      }

      return "";
    },

    // 
    calculateTotalHours(records) {
      return records
        .reduce((total, item) => total + Number(item.totalHours), 0)
        .toFixed(2);
    },

    // 
    showDetail(record) {
      this.currentDetail = { ...record };
      this.showDetailPopup = true;

      // 
      this.imageStates = {
        morningStartImg: record.morningStartImg ? "loading" : null,
        morningEndImg: record.morningEndImg ? "loading" : null,
        afternoonStartImg: record.afternoonStartImg ? "loading" : null,
        afternoonEndImg: record.afternoonEndImg ? "loading" : null,
      };

      // 
      this.imageTimestamp = {};
    },

    // 
    setImageState(field, state) {
      this.$set(this.imageStates, field, state);
    },

    // URL
    getImageUrl(field) {
      if (!this.currentDetail[field]) return "";

      // URL
      if (this.imageTimestamp[field]) {
        const separator = this.currentDetail[field].includes("?") ? "&" : "?";
        return `${this.currentDetail[field]}${separator}t=${this.imageTimestamp[field]}`;
      }

      return this.currentDetail[field];
    },

    // 
    reloadImage(field) {
      // 
      this.setImageState(field, "loading");

      // 
      this.imageTimestamp[field] = new Date().getTime();

      // 
      this.$forceUpdate();
    },

    // 
    handleImageError(field) {
      this.setImageState(field, "error");
    },
  },
};
</script>

