<template>
  <div class="page-container">
    <!--  -->
    <van-nav-bar title="" class="nav-bar" :border="false">
      <template #title>
        <span style="color: white; font-weight: 600"></span>
      </template>
    </van-nav-bar>

    <!--  -->
    <div class="card" style="margin: 30px 15px">
      <div class="card-body" style="padding: 30px 20px">
        <!--  -->
        <div style="text-align: center; margin-bottom: 25px">
          <h2
            style="margin-top: 15px; font-weight: 600; color: var(--text-color)"
          >
            
          </h2>
          <p style="color: var(--text-light); margin-top: 5px">
            
          </p>
        </div>

        <!--  -->
        <van-cell-group :border="false">
          <van-field
            v-model="username"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
            style="margin-bottom: 15px"
          />
          <van-field
            v-model="password"
            type="password"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />
        </van-cell-group>

        <!--  -->
        <van-button
          type="primary"
          class="btn-primary"
          style="width: 100%; margin-top: 25px; height: 48px; font-size: 16px"
          @click="handleLogin"
          :loading="loading"
        >
          
        </van-button>
      </div>
    </div>

    <!--  -->
    <div style="text-align: center; margin-top: 20px">
      <span style="color: var(--text-gray)"></span>
      <a
        @click="$router.push('/register')"
        style="color: var(--primary-color); font-weight: 500; margin-left: 5px"
        ></a
      >
    </div>
  </div>
</template>

<script>


import { Toast } from "vant";
import AV from "@/utils/leancloud";

export default {
  name: "LoginPage",
  data() {
    return {
      username: "",
      password: "",
      loading: false,
    };
  },
  methods: {
    async handleLogin() {
      try {
        if (!this.username || !this.password) {
          return Toast.fail("");
        }

        this.loading = true;
        await AV.User.logIn(this.username, this.password);
        Toast.success("");
        this.$router.push("/home");
      } catch (error) {
        Toast.fail("" + error.message);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>
