<script>
import { Toast } from "vant";
import AV from "@/utils/leancloud";
import Vue from "vue";

export default {
  name: "HomePage",
  data() {
    return {
      userInfo: {
        name: "",
        avatar: "",
      },
      defaultAvatar:
        "data:image/svg+xml;base64,",
      recentRecords: [],
      finished: false,
      showUserPopup: false,
    };
  },
  created() {
    // 
    if (!window.eventBus) {
      window.eventBus = new Vue();
    }

    // localStorage
    this.loadUserInfo();

    // 
    window.eventBus.$on("userInfoUpdated", (newInfo) => {
      this.userInfo = { ...this.userInfo, ...newInfo };
      // localStorage
      localStorage.setItem("userInfo", JSON.stringify(this.userInfo));
    });

    this.fetchRecentRecords();
  },
  beforeDestroy() {
    // 
    window.eventBus.$off("userInfoUpdated");
  },
  methods: {
    // 
    async loadUserInfo() {
      try {
        // localStorage
        const storedInfo = localStorage.getItem("userInfo");
        if (storedInfo) {
          this.userInfo = JSON.parse(storedInfo);
        }

        // LeanCloud
        const currentUser = AV.User.current();
        if (currentUser) {
          const cloudName =
            currentUser.get("nickname") || currentUser.getUsername() || "";
          const cloudAvatar = currentUser.get("avatar") || "";

          // 
          this.userInfo = {
            name: cloudName || this.userInfo.name || "",
            avatar: cloudAvatar || this.userInfo.avatar || "",
          };

          // localStorage
          localStorage.setItem("userInfo", JSON.stringify(this.userInfo));
        }
      } catch (error) {
        console.error("", error);
      }
    },

    async fetchRecentRecords() {
      try {
        const query = new AV.Query("WorkRecord");
        query.equalTo("userId", AV.User.current().id);
        query.descending("createdAt");
        query.limit(3);
        const results = await query.find();

        this.recentRecords = results.map((item) => ({
          name: item.get("personnelName") || item.get("name") || "",
          date: item.get("date"),
          morningStart: item.get("morningStart"),
          morningEnd: item.get("morningEnd"),
          afternoonStart: item.get("afternoonStart"),
          afternoonEnd: item.get("afternoonEnd"),
          totalHours: item.get("totalHours") || 0,
        }));
      } catch (error) {
        console.error("", error);
      }
    },

    showUserInfo() {
      this.showUserPopup = true;
    },

    async logout() {
      try {
        await AV.User.logOut();
        localStorage.removeItem("userInfo");
        this.$router.push("/login");
        Toast.success("");
      } catch (error) {
        console.error("", error);
        Toast.fail("");
      }
    },
  },
};
</script>

