            placeholder=""
            @click="showAfternoonEndPicker = true"
            style="margin-bottom: 15px"
            :class="{ 'field-error': !form.afternoonEnd }"
          />

          <!--  -->
          <div style="margin-top: 20px">
            <h4
              style="
                font-weight: 600;
                color: var(--text-color);
                margin-bottom: 15px;
              "
            >
              
              <span style="color: #f44; font-size: 14px"></span>
            </h4>

            <div
              style="display: grid; grid-template-columns: 1fr 1fr; gap: 15px"
            >
              <!--  -->
              <div
                style="
                  background: rgba(102, 126, 234, 0.05);
                  padding: 15px;
                  border-radius: 8px;
                "
              >
                <p
                  style="
                    font-size: 14px;
                    color: var(--text-gray);
                    margin-bottom: 10px;
                  "
                >
                  
                  <span v-if="!form.morningStartImg" style="color: #f44"
                    >*</span
                  >
                </p>
                <van-uploader
                  :after-read="(file) => handleUpload(file, 'morningStartImg')"
                  :before-delete="
                    () => {
                      form.morningStartImg = '';
                      return true;
                    }
                  "
                  :file-list="
                    form.morningStartImg ? [{ url: form.morningStartImg }] : []
                  "
                  accept="image/*"
                  max-count="1"
                  :before-read="beforeReadFile"
                  :upload-text="''"
                />
              </div>

              <!--  -->
              <div
                style="
                  background: rgba(102, 126, 234, 0.05);
                  padding: 15px;
                  border-radius: 8px;
                "
              >
                <p
                  style="
                    font-size: 14px;
                    color: var(--text-gray);
                    margin-bottom: 10px;
                  "
                >
                  
                  <span v-if="!form.morningEndImg" style="color: #f44">*</span>
                </p>
                <van-uploader
                  :after-read="(file) => handleUpload(file, 'morningEndImg')"
                  :before-delete="
                    () => {
                      form.morningEndImg = '';
                      return true;
                    }
                  "
                  :file-list="
                    form.morningEndImg ? [{ url: form.morningEndImg }] : []
                  "
                  accept="image/*"
                  max-count="1"
                  :before-read="beforeReadFile"
                  :upload-text="''"
                />
              </div>

              <!--  -->
              <div
                style="
                  background: rgba(102, 126, 234, 0.05);
                  padding: 15px;
                  border-radius: 8px;
                "
              >
                <p
                  style="
                    font-size: 14px;
                    color: var(--text-gray);
                    margin-bottom: 10px;
                  "
                >
                  
                  <span v-if="!form.afternoonStartImg" style="color: #f44"
                    >*</span
                  >
                </p>
                <van-uploader
                  :after-read="
                    (file) => handleUpload(file, 'afternoonStartImg')
                  "
                  :before-read="beforeReadFile"
                  :before-delete="
                    () => {
                      form.afternoonStartImg = '';
                      return true;
                    }
                  "
                  :file-list="
                    form.afternoonStartImg
                      ? [{ url: form.afternoonStartImg }]
                      : []
                  "
                  accept="image/*"
                  max-count="1"
                  :upload-text="''"
                />
              </div>

              <!--  -->
              <div
                style="
                  background: rgba(102, 126, 234, 0.05);
                  padding: 15px;
                  border-radius: 8px;
                "
              >
                <p
                  style="
                    font-size: 14px;
                    color: var(--text-gray);
                    margin-bottom: 10px;
                  "
                >
                  
                  <span v-if="!form.afternoonEndImg" style="color: #f44"
                    >*</span
                  >
                </p>
                <van-uploader
                  :after-read="(file) => handleUpload(file, 'afternoonEndImg')"
                  :before-delete="
                    () => {
                      form.afternoonEndImg = '';
                      return true;
                    }
                  "
                  :file-list="
                    form.afternoonEndImg ? [{ url: form.afternoonEndImg }] : []
                  "
                  accept="image/*"
                  max-count="1"
                  :before-read="beforeReadFile"
                  :upload-text="''"
                />
              </div>
            </div>
          </div>
        </van-cell-group>
      </div>
    </div>

    <!--  -->
    <van-button
      v-if="personnelList.length > 0 && !loadingPersonnel"
      type="primary"
      class="btn-primary"
      style="
        margin: 15px;
        width: calc(100% - 30px);
        height: 50px;
        font-size: 16px;
      "
      @click="submitRecord"
      :loading="submitting"
    >
      
    </van-button>

    <!--  -->
    <van-popup
      v-model="showPersonnelList"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <div class="personnel-select-popup">
        <div class="popup-header">
          <h3></h3>
          <van-button text @click="showPersonnelList = false"></van-button>
        </div>

        <div class="personnel-list">
          <van-cell
            v-for="(person, index) in personnelList"
            :key="index"
            :title="person.name"
            :label="person.department || ''"
            @click="selectPerson(person)"
            :class="{ selected: selectedPerson.id === person.id }"
          >
            <template #right-icon>
              <van-icon
                name="success"
                v-if="selectedPerson.id === person.id"
                style="color: var(--primary-color)"
              />
            </template>
          </van-cell>

          <div v-if="personnelList.length === 0" class="empty-list">
            <van-empty description="" />
          </div>
        </div>

        <div class="popup-footer">
          <van-button
            type="primary"
            block
            @click="confirmPersonnelSelect"
            :disabled="!selectedPerson.id"
          >
            
          </van-button>
        </div>
      </div>
    </van-popup>

    <!--  -->
    <van-popup v-model="showDatePicker" position="bottom">
      <van-datetime-picker
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        :value="dateValue"
        @confirm="onDateConfirm"
        @cancel="showDatePicker = false"
      />
    </van-popup>

    <!--  -->
    <van-popup v-model="showMorningStartPicker" position="bottom">
      <van-datetime-picker
        type="time"
        title=""
        :value="morningStartValue"
        @confirm="(value) => onTimeConfirm(value, 'morningStart')"
        @cancel="showMorningStartPicker = false"
        format="HH:mm"
      />
    </van-popup>

    <!--  -->
    <van-popup v-model="showMorningEndPicker" position="bottom">
      <van-datetime-picker
        type="time"
        title=""
        :value="morningEndValue"
        @confirm="(value) => onTimeConfirm(value, 'morningEnd')"
        @cancel="showMorningEndPicker = false"
        format="HH:mm"
      />
    </van-popup>

    <!--  -->
    <van-popup v-model="showAfternoonStartPicker" position="bottom">
      <van-datetime-picker
        type="time"
        title=""
        :value="afternoonStartValue"
        @confirm="(value) => onTimeConfirm(value, 'afternoonStart')"
        @cancel="showAfternoonStartPicker = false"
        format="HH:mm"
      />
    </van-popup>

    <!--  -->
    <van-popup v-model="showAfternoonEndPicker" position="bottom">
      <van-datetime-picker
        type="time"
        title=""
        :value="afternoonEndValue"
        @confirm="(value) => onTimeConfirm(value, 'afternoonEnd')"
        @cancel="showAfternoonEndPicker = false"
        format="HH:mm"
      />
    </van-popup>
  </div>
</template>

<script>
import { Toast } from "vant";
import moment from "moment";
import AV from "@/utils/leancloud";

export default {
  name: "WorkRecord",

  data() {
    return {
      active: 1,
      submitting: false,
      loadingPersonnel: false,
      // 
      form: {
        date: moment().format("YYYY-MM-DD"),
        morningStart: "",
        morningEnd: "",
        afternoonStart: "",
        afternoonEnd: "",
        morningStartImg: "",
        morningEndImg: "",
        afternoonStartImg: "",
        afternoonEndImg: "",
      },
      // 
      selectedPerson: {
        id: "",
        name: "",
        phone: "",
        department: "",
      },
      // 
      personnelList: [],
      showPersonnelList: false,
      // 
      showDatePicker: false,
      dateValue: new Date(),
      minDate: new Date("2020-01-01"),
      maxDate: new Date(),
      // 
      showMorningStartPicker: false,
      showMorningEndPicker: false,
      showAfternoonStartPicker: false,
      showAfternoonEndPicker: false,
      morningStartValue: new Date(),
      morningEndValue: new Date(),
      afternoonStartValue: new Date(),
      afternoonEndValue: new Date(),
    };
  },
  created() {
    this.dateValue = new Date();
    this.fetchPersonnelList();
  },
  methods: {
    // base64Blob
    base64ToBlob(base64Data) {
      try {
        // base64data:image/xxx;base64,
        const base64Parts = base64Data.split(",");
        const contentType = base64Parts[0].split(":")[1].split(";")[0];
        const raw = window.atob(base64Parts[1]);
        const rawLength = raw.length;

        const uInt8Array = new Uint8Array(rawLength);
        for (let i = 0; i < rawLength; ++i) {
          uInt8Array[i] = raw.charCodeAt(i);
        }

        return new Blob([uInt8Array], { type: contentType });
      } catch (error) {
        console.error("Base64Blob", error);
        return null;
      }
    },

    // 
    beforeReadFile(file) {
      console.log("", file);

      // 
      if (file.type && !file.type.startsWith("image/")) {
        Toast.fail("");
        return false;
      }

      // 5MB
      const maxSize = 5 * 1024 * 1024; // 5MB
      if (file.size && file.size > maxSize) {
        Toast.fail("5MB");
        return false;
      }

      return true;
    },

    // 
    async fetchPersonnelList() {
      try {
        this.loadingPersonnel = true;

        // 
        const query = new AV.Query("Personnel");
        query.equalTo("userId", AV.User.current().id);
        query.descending("createdAt");
        const results = await query.find();

        this.personnelList = results.map((item) => ({
          id: item.id,
          name: item.get("name"),
          phone: item.get("phone"),
          department: item.get("department"),
        }));

        // 
        if (this.personnelList.length > 0 && !this.selectedPerson.id) {
          this.selectedPerson = { ...this.personnelList[0] };
        }
      } catch (error) {
        console.error("", error);

        if (error.code === 101) {
          this.personnelList = [];
          Toast.info("");
        } else if (error.message.includes("permission")) {
          Toast.fail("");
        } else {
          this.personnelList = [];
          Toast.info("");
        }
      } finally {
        this.loadingPersonnel = false;
      }
    },

    // 
    selectPerson(person) {
      this.selectedPerson = { ...person };
    },

    // 
    confirmPersonnelSelect() {
      if (this.selectedPerson.id) {
        this.showPersonnelList = false;
      } else {
        Toast.fail("");
      }
    },

    // 
    onDateConfirm(date) {
      this.form.date = moment(date).format("YYYY-MM-DD");
      this.showDatePicker = false;
    },

    // 
    onTimeConfirm(time, field) {
      console.log("", time, "", field);

      // 1timeDate
      if (time instanceof Date) {
        const timeStr = moment(time).format("HH:mm");
        this.form[field] = timeStr;
        this[`${field}Value`] = time;
      }
      // 2time
      else if (typeof time === "string") {
        this.form[field] = time;
        // Date
        const today = moment().format("YYYY-MM-DD");
        this[`${field}Value`] = new Date(`${today} ${time}`);
      }

      this[
        `show${field.charAt(0).toUpperCase() + field.slice(1)}Picker`
      ] = false;
    },

    // 
    async handleUpload(file, field) {
      try {
        console.log("", file, "", field);

        // 
        if (!file) {
          Toast.fail("");
          return;
        }

        Toast.loading({ message: "...", forbidClick: true });

        let fileObject;
        let fileName = file.name || `upload_${Date.now()}.jpg`;

        // 
        if (file.file instanceof File) {
          // File
          fileObject = file.file;
          fileName = file.file.name || fileName;
        } else if (file.originFileObj instanceof File) {
          // File
          fileObject = file.originFileObj;
          fileName = file.originFileObj.name || fileName;
        } else if (
          typeof file.content === "string" &&
          file.content.startsWith("data:")
        ) {
          // base64Blob
          console.log("base64Blob");
          fileObject = this.base64ToBlob(file.content);
          if (!fileObject) {
            throw new Error("Base64");
          }
        } else if (file.content instanceof Blob) {
          // Blob
          fileObject = file.content;
        } else {
          throw new Error("");
        }

        console.log("", fileObject, "", typeof fileObject);

        // LeanCloud
        const avFile = new AV.File(fileName, fileObject);

        // 
        const uploadedFile = await avFile.save();

        console.log("", uploadedFile);

        // URL
        this.form[field] = uploadedFile.url();

        Toast.success("");
      } catch (error) {
        console.error("", error);

        if (error.message.includes("empty")) {
          Toast.fail("");
        } else if (error.message.includes("not supported")) {
          Toast.fail("");
        } else if (error.message.includes("Base64")) {
          Toast.fail("");
        } else {
          Toast.fail(`${error.message || ""}`);
        }
      }
    },

    // 
    validateForm() {
      if (this.personnelList.length === 0) {
        Toast.fail("");
        this.$router.push("/personnel");
        return false;
      }

      if (!this.selectedPerson.id) {
        Toast.fail("");
        return false;
      }

      if (!this.form.date) {
        Toast.fail("");
        return false;
      }

      if (!this.form.morningStart) {
        Toast.fail("");
        return false;
      }

      if (!this.form.morningEnd) {
        Toast.fail("");
        return false;
      }

      if (!this.form.afternoonStart) {
        Toast.fail("");
        return false;
      }

      if (!this.form.afternoonEnd) {
        Toast.fail("");
        return false;
      }

      if (!this.form.morningStartImg) {
        Toast.fail("");
        return false;
      }

      if (!this.form.morningEndImg) {
        Toast.fail("");
        return false;
      }

      if (!this.form.afternoonStartImg) {
        Toast.fail("");
        return false;
      }

      if (!this.form.afternoonEndImg) {
        Toast.fail("");
        return false;
      }

      // 
      if (this.form.morningStart && this.form.morningEnd) {
        const start = moment(this.form.morningStart, "HH:mm");
        const end = moment(this.form.morningEnd, "HH:mm");
        if (start.isAfter(end)) {
          Toast.fail("");
          return false;
        }
      }

      if (this.form.afternoonStart && this.form.afternoonEnd) {
        const start = moment(this.form.afternoonStart, "HH:mm");
        const end = moment(this.form.afternoonEnd, "HH:mm");
        if (start.isAfter(end)) {
          Toast.fail("");
          return false;
        }
      }

      return true;
    },

    // 
    async submitRecord() {
      try {
        if (!this.validateForm()) {
          return;
        }

        this.submitting = true;

        const WorkRecord = AV.Object.extend("WorkRecord");
        const record = new WorkRecord();

        record.set("personnelId", this.selectedPerson.id);
        record.set("personnelName", this.selectedPerson.name);
        record.set("personnelDepartment", this.selectedPerson.department);
        record.set("date", this.form.date);
        record.set("morningStart", this.form.morningStart);
        record.set("morningEnd", this.form.morningEnd);
        record.set("afternoonStart", this.form.afternoonStart);
        record.set("afternoonEnd", this.form.afternoonEnd);
        record.set("morningStartImg", this.form.morningStartImg);
        record.set("morningEndImg", this.form.morningEndImg);
        record.set("afternoonStartImg", this.form.afternoonStartImg);
        record.set("afternoonEndImg", this.form.afternoonEndImg);
        record.set("userId", AV.User.current().id);

        // 
        const morningHours = this.calculateHours(
          this.form.morningStart,
          this.form.morningEnd
        );
        const afternoonHours = this.calculateHours(
          this.form.afternoonStart,
          this.form.afternoonEnd
        );
        const totalHours = morningHours + afternoonHours;

        record.set("morningHours", morningHours);
        record.set("afternoonHours", afternoonHours);
        record.set("totalHours", totalHours);

        await record.save();

        Toast.success("");

        // 
        this.resetForm();
      } catch (error) {
        console.error("", error);
        Toast.fail("" + error.message);
      } finally {
        this.submitting = false;
      }
    },

    // 
    calculateHours(startTime, endTime) {
      if (!startTime || !endTime) return 0;

      const start = moment(startTime, "HH:mm");
      const end = moment(endTime, "HH:mm");

      const duration = moment.duration(end.diff(start));
      return parseFloat(duration.asHours().toFixed(2));
    },

    // 
    resetForm() {
      this.form = {
        date: moment().format("YYYY-MM-DD"),
        morningStart: "",
        morningEnd: "",
        afternoonStart: "",
        afternoonEnd: "",
        morningStartImg: "",
        morningEndImg: "",
        afternoonStartImg: "",
        afternoonEndImg: "",
      };

      const now = new Date();
      this.dateValue = now;
      this.morningStartValue = now;
      this.morningEndValue = now;
      this.afternoonStartValue = now;
      this.afternoonEndValue = now;
    },
  },
};
</script>

