<template>
  <div class="setting-container">
    <!--  -->
    <van-nav-bar
      title=""
      class="nav-bar"
      left-arrow
      @click-left="$router.go(-1)"
      border="false"
    >
      <template #title>
        <span style="color: white; font-weight: 600"></span>
      </template>
    </van-nav-bar>

    <!--  -->
    <div class="user-card">
      <!--  -->
      <div class="avatar-wrapper" @click="showAvatarPopup = true">
        <div class="avatar-container">
          <!-- img -->
          <img :src="getAvatarUrl()" class="user-avatar" alt="" />
          <div class="avatar-edit-icon">
            <van-icon name="camera-o" size="16" color="white" />
          </div>
        </div>
        <p class="avatar-tip"></p>
      </div>

      <!--  -->
      <div class="nickname-wrapper" @click="showNicknamePopup = true">
        <div class="nickname-content">
          <span class="nickname-label"></span>
          <span class="nickname-value">{{
            userInfo.name || ""
          }}</span>
          <van-icon name="arrow" size="14" color="#969799" />
        </div>
        <p class="nickname-tip"></p>
      </div>
    </div>

    <!--  -->
    <van-cell-group class="setting-list">
      <van-cell
        title=""
        is-link
        icon="lock-o"
        @click="showChangePwd = true"
      />
      <van-cell title="" is-link icon="info-o" @click="showAbout" />
      <van-cell
        title=""
        is-link
        icon="arrow-left"
        class="logout-cell"
        @click="confirmLogout"
      />
    </van-cell-group>

    <!--  -->
    <van-popup
      v-model="showNicknamePopup"
      position="bottom"
      :style="{ height: '40%' }"
      round
    >
      <div class="popup-header">
        <h3></h3>
        <van-button text @click="showNicknamePopup = false" icon="cross" />
      </div>
      <div class="form-container">
        <van-form @submit="submitNickname">
          <van-field
            v-model="tempNickname"
            name="nickname"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />

          <div class="form-actions">
            <van-button
              type="default"
              @click="showNicknamePopup = false"
              class="cancel-btn"
              ></van-button
            >
            <van-button
              type="primary"
              native-type="submit"
              :loading="submitting"
              ></van-button
            >
          </div>
        </van-form>
      </div>
    </van-popup>

    <!--  -->
    <van-popup
      v-model="showChangePwd"
      position="bottom"
      :style="{ height: '70%' }"
      round
    >
      <div class="popup-header">
        <h3></h3>
        <van-button text @click="showChangePwd = false" icon="cross" />
      </div>
      <div class="form-container">
        <van-form @submit="submitChangePwd">
          <van-field
            v-model="oldPassword"
            type="password"
            name="oldPassword"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />
          <van-field
            v-model="newPassword"
            type="password"
            name="newPassword"
            label=""
            placeholder=""
            :rules="[
              { required: true, message: '' },
              { min: 6, message: '6' },
            ]"
          />
          <van-field
            v-model="confirmPassword"
            type="password"
            name="confirmPassword"
            label=""
            placeholder=""
            :rules="[
              { required: true, message: '' },
              {
                validator: validateConfirmPassword,
                message: '',
              },
            ]"
          />

          <div class="form-actions">
            <van-button type="default" @click="resetPwdForm" class="cancel-btn"
              ></van-button
            >
            <van-button
              type="primary"
              native-type="submit"
              :loading="pwdSubmitting"
              ></van-button
            >
          </div>
        </van-form>
      </div>
    </van-popup>

    <!--  -->
    <van-popup
      v-model="showAvatarPopup"
      position="center"
      :style="{ width: '90%', borderRadius: '12px' }"
    >
      <div class="avatar-popup-content">
        <h3 class="popup-title"></h3>
        <div class="avatar-preview-container">
          <img
            :src="tempAvatar || userInfo.avatar || defaultAvatar"
            class="avatar-preview"
            alt=""
          />
        </div>
        <van-uploader
          :after-read="handleAvatarUpload"
          :before-read="beforeAvatarRead"
          accept="image/*"
          max-count="1"
          class="avatar-uploader"
        />
        <div class="popup-actions">
          <van-button
            type="default"
            @click="showAvatarPopup = false"
            class="popup-btn"
            ></van-button
          >
          <van-button
            type="primary"
            @click="submitAvatarChange"
            :loading="submitting"
            class="popup-btn"
            ></van-button
          >
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import AV from "@/utils/leancloud";

export default {
  name: "SettingPage",
  data() {
    return {
      // 
      userInfo: {
        name: "",
        avatar: "",
        email: "",
      },
      defaultAvatar:
        "data:image/svg+xml;base64,",
      avatarTimestamp: Date.now(), // 

      // 
      showNicknamePopup: false,
      showChangePwd: false,
      showAvatarPopup: false,

      // 
      tempNickname: "",
      tempAvatar: "",
      submitting: false,

      // 
      oldPassword: "",
      newPassword: "",
      confirmPassword: "",
      pwdSubmitting: false,
    };
  },
  created() {
    this.loadUserInfo();
  },
  methods: {
    // URL
    getAvatarUrl() {
      const baseUrl = this.userInfo.avatar || this.defaultAvatar;
      // 
      if (baseUrl.startsWith("http")) {
        return `${baseUrl}?t=${this.avatarTimestamp}`;
      }
      return baseUrl;
    },

    // 
    async loadUserInfo() {
      try {
        const currentUser = AV.User.current();
        if (currentUser) {
          this.userInfo = {
            name:
              currentUser.get("nickname") || currentUser.getUsername() || "",
            avatar: currentUser.get("avatar") || "",
            email: currentUser.getEmail() || "",
          };

          // 
          this.tempNickname = this.userInfo.name;
          this.tempAvatar = this.userInfo.avatar;

          // 
          this.avatarTimestamp = Date.now();
        }
      } catch (error) {
        console.error("", error);
      }
    },

    // 
    beforeAvatarRead(file) {
      const maxSize = 2 * 1024 * 1024; // 2MB
      if (file.size > maxSize) {
        Toast.fail("2MB");
        return false;
      }
      if (!file.type.startsWith("image/")) {
        Toast.fail("");
        return false;
      }
      return true;
    },

    // 
    async handleAvatarUpload(file) {
      try {
        Toast.loading({ message: "...", forbidClick: true });

        // 
        let fileObject;
        if (file.file instanceof File) {
          fileObject = file.file;
        } else if (file.content instanceof Blob) {
          fileObject = file.content;
        } else if (
          typeof file.content === "string" &&
          file.content.startsWith("data:")
        ) {
          // base64Blob
          const base64Parts = file.content.split(",");
          const contentType = base64Parts[0].split(":")[1].split(";")[0];
          const raw = window.atob(base64Parts[1]);
          const uInt8Array = new Uint8Array(raw.length);
          for (let i = 0; i < raw.length; i++) {
            uInt8Array[i] = raw.charCodeAt(i);
          }
          fileObject = new Blob([uInt8Array], { type: contentType });
        }

        // LeanCloudACL
        const avFile = new AV.File(`avatar_${Date.now()}.png`, fileObject);

        // ACL
        const acl = new AV.ACL();
        acl.setPublicReadAccess(true); // 
        acl.setWriteAccess(AV.User.current(), true); // 
        avFile.setACL(acl);

        const uploadedFile = await avFile.save();

        this.tempAvatar = uploadedFile.url();
        Toast.success("");
      } catch (error) {
        console.error("", error);
        Toast.fail("");
      }
    },

    //  - 
    async submitAvatarChange() {
      if (!this.tempAvatar) {
        Toast.fail("");
        return;
      }

      try {
        this.submitting = true;
        const currentUser = AV.User.current();

        // 
        currentUser.set("avatar", this.tempAvatar);

        // ACL
        const acl = new AV.ACL();
        acl.setReadAccess(AV.User.current(), true);
        acl.setWriteAccess(AV.User.current(), true);
        currentUser.setACL(acl);

        // 
        await currentUser.save(null, { useMasterKey: true });

        // 
        this.userInfo.avatar = this.tempAvatar;
        this.avatarTimestamp = Date.now(); // 

        // 
        this.$forceUpdate();

        this.showAvatarPopup = false;

        // 
        this.notifyHomeUpdate();

        Toast.success("");
      } catch (error) {
        console.error("", error);
        if (error.code === 403) {
          try {
            // 
            const userQuery = new AV.Query("_User");
            userQuery.equalTo("objectId", AV.User.current().id);

            const users = await userQuery.find({ useMasterKey: true });
            if (users.length > 0) {
              const user = users[0];
              user.set("avatar", this.tempAvatar);

              const acl = new AV.ACL();
              acl.setReadAccess(user, true);
              acl.setWriteAccess(user, true);
              user.setACL(acl);

              await user.save(null, { useMasterKey: true });

              // 
              this.userInfo.avatar = this.tempAvatar;
              this.avatarTimestamp = Date.now();
              this.$forceUpdate();

              this.showAvatarPopup = false;
              this.notifyHomeUpdate();
              Toast.success("");
            }
          } catch (secondError) {
            console.error("", secondError);
            Toast.fail("");
          }
        } else {
          Toast.fail("");
        }
      } finally {
        this.submitting = false;
      }
    },

    // 
    async submitNickname() {
      if (!this.tempNickname.trim()) {
        Toast.fail("");
        return;
      }

      try {
        this.submitting = true;
        const currentUser = AV.User.current();

        currentUser.set("nickname", this.tempNickname.trim());

        const acl = new AV.ACL();
        acl.setReadAccess(currentUser, true);
        acl.setWriteAccess(currentUser, true);
        currentUser.setACL(acl);

        await currentUser.save(null, { useMasterKey: true });

        this.userInfo.name = this.tempNickname.trim();
        this.showNicknamePopup = false;

        this.notifyHomeUpdate();

        Toast.success("");
      } catch (error) {
        console.error("", error);
        if (error.code === 403) {
          try {
            const userQuery = new AV.Query("_User");
            userQuery.equalTo("objectId", AV.User.current().id);

            const users = await userQuery.find({ useMasterKey: true });
            if (users.length > 0) {
              const user = users[0];
              user.set("nickname", this.tempNickname.trim());

              const acl = new AV.ACL();
              acl.setReadAccess(user, true);
              acl.setWriteAccess(user, true);
              user.setACL(acl);

              await user.save(null, { useMasterKey: true });

              this.userInfo.name = this.tempNickname.trim();
              this.showNicknamePopup = false;
              this.notifyHomeUpdate();
              Toast.success("");
            }
          } catch (secondError) {
            console.error("", secondError);
            Toast.fail("");
          }
        } else {
          Toast.fail("" + error.message);
        }
      } finally {
        this.submitting = false;
      }
    },

    // 
    validateConfirmPassword(val) {
      return val === this.newPassword;
    },

    // 
    async submitChangePwd() {
      try {
        this.pwdSubmitting = true;

        const currentUser = AV.User.current();

        // 
        await AV.User.logIn(currentUser.getUsername(), this.oldPassword);

        // 
        currentUser.setPassword(this.newPassword);

        await currentUser.save(null, { useMasterKey: true });

        // 
        this.resetPwdForm();
        this.showChangePwd = false;

        Toast.success("");

        // 
        setTimeout(() => {
          this.logout();
        }, 1500);
      } catch (error) {
        console.error("", error);
        if (error.code === 211) {
          Toast.fail("");
        } else if (error.code === 403) {
          Toast.fail("");
        } else {
          Toast.fail("" + error.message);
        }
      } finally {
        this.pwdSubmitting = false;
      }
    },

    // 
    resetPwdForm() {
      this.oldPassword = "";
      this.newPassword = "";
      this.confirmPassword = "";
      this.showChangePwd = false;
    },

    // 
    confirmLogout() {
      Dialog.confirm({
        title: "",
        message: "",
        confirmButtonText: "",
        cancelButtonText: "",
        confirmButtonColor: "#f44",
      }).then(() => {
        this.logout();
      });
    },

    // 
    async logout() {
      try {
        await AV.User.logOut();
        localStorage.removeItem("userInfo");
        this.$router.push("/login");
        Toast.success("");
      } catch (error) {
        console.error("", error);
        Toast.fail("");
      }
    },

    // 
    showAbout() {
      Dialog.alert({
        title: "",
        message: " v1.0\n\n",
        confirmButtonText: "",
      });
    },

    // 
    notifyHomeUpdate() {
      // 
      if (window.eventBus) {
        window.eventBus.$emit("userInfoUpdated", {
          name: this.userInfo.name,
          avatar: this.userInfo.avatar,
        });
      }

      // localStorage
      localStorage.setItem(
        "userInfo",
        JSON.stringify({
          name: this.userInfo.name,
          avatar: this.userInfo.avatar,
        })
      );
    },
  },
};
</script>

