<template>
  <div class="personnel-container">
    <!--  -->
    <van-nav-bar
      title=""
      class="nav-bar"
      left-arrow
      @click-left="$router.go(-1)"
      border="false"
    >
      <template #right>
        <!--  -->
        <van-button
          type="primary"
          size="small"
          icon="plus"
          @click="showAddPersonnel = true"
          class="add-btn"
        >
          
        </van-button>
      </template>
      <template #title>
        <span style="color: white; font-weight: 600"></span>
      </template>
    </van-nav-bar>

    <!--  -->
    <div class="search-bar">
      <van-search
        v-model="searchValue"
        placeholder="/"
        @search="onSearch"
        @clear="onSearchClear"
        background="#f8f9fa"
        shape="round"
      />
    </div>

    <!--  -->
    <div class="stats-card" v-if="filteredPersonnelList.length > 0">
      <div class="stat-item">
        <span class="stat-label"></span>
        <span class="stat-value">{{ filteredPersonnelList.length }}</span>
      </div>
      <div class="stat-item">
        <span class="stat-label"></span>
        <span class="stat-value">{{ latestPersonnel || "-" }}</span>
      </div>
    </div>

    <!--  -->
    <div class="personnel-list" v-if="filteredPersonnelList.length > 0">
      <div
        class="personnel-card"
        v-for="(person, index) in filteredPersonnelList"
        :key="index"
      >
        <div class="card-header">
          <div class="avatar">
            <van-icon name="user-o" size="24" color="#667eea" />
          </div>
          <div class="name-phone">
            <h3 class="person-name">{{ person.name }}</h3>
            <p class="phone-text">{{ person.phone }}</p>
          </div>
        </div>

        <div class="card-body">
          <div class="info-row" v-if="person.position">
            <van-icon name="briefcase-o" size="14" class="info-icon" />
            <span>{{ person.position }}</span>
          </div>
          <div class="info-row" v-if="person.remark">
            <van-icon name="comment-o" size="14" class="info-icon" />
            <span>{{ person.remark }}</span>
          </div>
          <div class="info-row">
            <van-icon name="calendar-o" size="14" class="info-icon" />
            <span>{{ formatDate(person.createdAt) }}</span>
          </div>
        </div>

        <div class="card-actions">
          <van-button
            size="mini"
            type="default"
            @click="editPersonnel(person)"
            class="action-btn edit-btn"
          >
            
          </van-button>
          <van-button
            size="mini"
            type="danger"
            @click="deletePersonnel(person.id)"
            class="action-btn delete-btn"
          >
            
          </van-button>
        </div>
      </div>
    </div>

    <!-- Vant -->
    <div
      class="empty-state"
      v-if="filteredPersonnelList.length === 0 && !loading"
    >
      <van-empty
        description=""
        icon="user-o"
        icon-size="60"
        style="--van-empty-icon-color: #667eea; --van-empty-text-color: #969799"
      />
      <van-button
        type="primary"
        size="large"
        @click="showAddPersonnel = true"
        class="add-first-btn"
      >
        <van-icon name="plus" /> 
      </van-button>
    </div>

    <!--  -->
    <van-loading v-if="loading" class="loading-spinner" vertical size="24">
      ...
    </van-loading>

    <!-- / -->
    <van-popup
      v-model="showAddPersonnel"
      position="bottom"
      :style="{ height: '70%' }"
      round
      class="form-popup"
    >
      <div class="popup-header">
        <h2>{{ isEditing ? "" : "" }}</h2>
        <van-button
          text
          @click="showAddPersonnel = false"
          icon="cross"
          class="close-btn"
        />
      </div>

      <div class="form-container">
        <van-form @submit="submitPersonnelForm" class="personnel-form">
          <!--  -->
          <van-field
            v-model="form.name"
            name="name"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
            required
            class="required-field"
          />

          <!--  -->
          <van-field
            v-model="form.phone"
            name="phone"
            label=""
            placeholder=""
            :rules="[
              { required: true, message: '' },
            ]"
            required
            class="required-field"
          />

          <!--  -->
          <van-field
            v-model="form.position"
            name="position"
            label=""
            placeholder=""
          />

          <!--  -->
          <van-field
            v-model="form.remark"
            name="remark"
            label=""
            type="textarea"
            placeholder=""
            rows="3"
          />

          <div class="form-actions">
            <van-button
              type="default"
              @click="showAddPersonnel = false"
              class="cancel-btn"
            >
              
            </van-button>
            <van-button
              type="primary"
              native-type="submit"
              :loading="submitting"
              class="submit-btn"
            >
              {{ isEditing ? "" : "" }}
            </van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  Dialog,
  Toast,
} from "vant";
import AV from "@/utils/leancloud";
import moment from "moment";

export default {
  name: 'PersonnelList',
  data() {
    return {
      loading: false,
      submitting: false,
      personnelList: [],
      filteredPersonnelList: [],
      searchValue: "",
      showAddPersonnel: false,
      isEditing: false,
      currentPersonnelId: "",
      form: {
        name: "",
        phone: "",
        position: "",
        remark: "",
      },
    };
  },
  computed: {
    latestPersonnel() {
      if (this.filteredPersonnelList.length === 0) return "";
      return this.filteredPersonnelList[0].name;
    },
  },
  created() {
    this.fetchPersonnelList();
  },
  methods: {
    // 
    async fetchPersonnelList() {
      try {
        this.loading = true;

        const query = new AV.Query("Personnel");
        query.equalTo("userId", AV.User.current().id);
        query.descending("createdAt");
        const results = await query.find();

        this.personnelList = results.map((item) => ({
          id: item.id,
          name: item.get("name"),
          phone: item.get("phone"),
          position: item.get("position") || "",
          remark: item.get("remark") || "",
          createdAt: item.createdAt,
        }));

        this.filteredPersonnelList = [...this.personnelList];
      } catch (error) {
        console.error("", error);
        Toast.fail("");
      } finally {
        this.loading = false;
      }
    },

    // 
    onSearch() {
      if (!this.searchValue) {
        this.filteredPersonnelList = [...this.personnelList];
        return;
      }

      const keyword = this.searchValue.toLowerCase();
      this.filteredPersonnelList = this.personnelList.filter(
        (person) =>
          person.name.toLowerCase().includes(keyword) ||
          (person.phone && person.phone.includes(keyword))
      );
    },

    // 
    onSearchClear() {
      this.filteredPersonnelList = [...this.personnelList];
    },

    // 
    formatDate(date) {
      return moment(date).format("YYYY-MM-DD");
    },

    // 
    editPersonnel(person) {
      this.isEditing = true;
      this.currentPersonnelId = person.id;
      this.form = {
        name: person.name,
        phone: person.phone || "",
        position: person.position || "",
        remark: person.remark || "",
      };
      this.showAddPersonnel = true;
    },

    // 
    async deletePersonnel(id) {
      try {
        await Dialog.confirm({
          title: "",
          message: "",
          confirmButtonText: "",
          cancelButtonText: "",
          confirmButtonColor: "#f44",
        });

        const personnel = AV.Object.createWithoutData("Personnel", id);
        await personnel.destroy();
        Toast.success("");
        this.fetchPersonnelList();
      } catch (error) {
        if (error !== "cancel") {
          console.error("", error);
          Toast.fail("");
        }
      }
    },

    // 
    async submitPersonnelForm() {
      try {
        this.submitting = true;

        if (this.isEditing) {
          // 
          const personnel = AV.Object.createWithoutData(
            "Personnel",
            this.currentPersonnelId
          );
          personnel.set("name", this.form.name);
          personnel.set("phone", this.form.phone);
          personnel.set("position", this.form.position);
          personnel.set("remark", this.form.remark);
          await personnel.save();
          Toast.success("");
        } else {
          // 
          const Personnel = AV.Object.extend("Personnel");
          const personnel = new Personnel();

          personnel.set("name", this.form.name);
          personnel.set("phone", this.form.phone);
          personnel.set("position", this.form.position);
          personnel.set("remark", this.form.remark);
          personnel.set("userId", AV.User.current().id);

          await personnel.save();
          Toast.success("");
        }

        this.showAddPersonnel = false;
        this.fetchPersonnelList();
        this.resetForm();
      } catch (error) {
        console.error("", error);
        Toast.fail(this.isEditing ? "" : "");
      } finally {
        this.submitting = false;
      }
    },

    // 
    resetForm() {
      this.form = {
        name: "",
        phone: "",
        position: "",
        remark: "",
      };
      this.isEditing = false;
      this.currentPersonnelId = "";
    },
  },
};
</script>

